import React, { useState } from 'react';
import { useEffect } from 'react';

import './index.scss';

interface ModalProps {
  isOpen: boolean
  onDismiss: () => void
  minHeight?: number | false
  maxHeight?: number
  initialFocusRef?: React.RefObject<any>
  children?: React.ReactNode
  title?: string
}

export default function Modal({
  isOpen,
  onDismiss,
  children,
  title,
}: ModalProps) {
  const [isOpenState, setIsOpen] = useState(isOpen)

  const onCloseClick = () => {
    setIsOpen(false);
    onDismiss();
  }

  useEffect(() => {
    setIsOpen(isOpen)
  }, [isOpen])

  return (
    <div style={{display: isOpenState ? 'block' : 'none'}} className='nes-dialog-container'>
      <dialog className="nes-dialog is-rounded">
        <div>
          <p className='title'>
            <span>{title}</span>
            <i className='nes-icon is-small close' onClick={onCloseClick}></i>
          </p>
          {children}
        </div>
      </dialog>
      <div className='backdrop' onClick={onCloseClick}></div>
    </div>
  )
}
