import React, { useState, useEffect, useRef } from 'react';
// import { useHistory, RouteComponentProps } from 'react-router-dom';

// components

// images
import catImg from '@/assets/p.png';
import popcatImg from '@/assets/op.png';

// sound
// import popSound from '@/assets/audio/cartoon-pop.wav';
// import popSound from '@/assets/audio/actmsg.wav';
import pop1 from '@/assets/audio/pop1.mp3';
import pop2 from '@/assets/audio/pop2.mp3';
import pop3 from '@/assets/audio/pop3.mp3';
import pop4 from '@/assets/audio/pop4.mp3';

// styles
import './index.scss';

interface propType {
  className?: string;
}

export interface attachmentsType {}

interface popText {
  id: string | number;
  text: string;
  type?: number;
  anchor?: {
    x?: number;
    y?: number;
  };
}

const popSounds = [pop1, pop2, pop3, pop4];

let lastPop = Date.now();
const popDelay = 33;

/**
 * @component Popcat
 * @function Popcat - Renders a <Popcat /> component
 * @param {object} props - component 參數
 */
const Popcat: React.FC<propType> & attachmentsType = ({ className }) => {
  const audioRefs = popSounds.map(() => useRef<HTMLAudioElement>(null));
  const popcat = useRef<HTMLImageElement>(null);

  const [popTexts, setPopText] = useState<popText[]>([]);
  const uidRef = useRef({ id: 0 });

  const [open, setOpen] = useState(false);

  const [popSoundIndex, setPopSoundIndex] = useState(0);

  const onPress = async (e?: any) => {
    const now = Date.now();
    if (now < lastPop + popDelay) {
      return;
    }
    lastPop = now;

    audioRefs[popSoundIndex].current?.play();
    setPopSoundIndex(
      [0, 1, 2, 3].filter((_, index) => popSoundIndex !== index)[
        Math.floor(Math.random() * (popSounds.length - 1))
      ]
    );

    particleAdd({
      text: '+1',
      type: Math.floor(Math.random() * 3),
      anchor: e
    });

    setOpen(true);
  };

  const onRelease = async (e?: any) => {
    setOpen(false);
  };

  const particleAdd = ({
    text,
    type,
    anchor
  }: {
    text: string;
    type?: number;
    anchor?: any;
  }) => {
    setPopText([
      ...popTexts,
      {
        id: uidRef?.current?.id ?? 0,
        text: text ?? '+1',
        type: type ?? 0,
        anchor: {
          x:
            anchor?.clientX ??
            anchor?.touches?.[0]?.clientX ??
            window.innerWidth / 2,
          y: (
            anchor?.clientY ??
            anchor?.touches?.[0]?.clientY ??
            window.innerHeight / 2
          ) - 24
        }
      }
    ]);
    uidRef!.current!.id += 1;
  };

  const particleRemove = (targetId: string | number) => {
    const list = [...popTexts];
    const index = list.findIndex(({ id }) => id === targetId);
    if (index !== -1) {
      list.splice(index, 1);
      setPopText(list);
    }
  };

  useEffect(() => {
    const onPressProxy = () => {
      onPress();
    };
    const onReleaseProxy = () => {
      onRelease();
    };
    document.addEventListener('keydown', onPressProxy);
    document.addEventListener('keyup', onReleaseProxy);

    // this line can remove duplicate listener
    return () => {
      document.removeEventListener('keydown', onPressProxy);
      document.removeEventListener('keyup', onReleaseProxy);
    };
  }, [popTexts]);

  return (
    <div
      style={{
        backgroundImage: `url('${catImg}')`
      }}
      className={['corn-popcat-container', className].join(' ')}
      onPointerDown={onPress}
      onPointerUp={onRelease}
    >
      {audioRefs.map((audioRef, index) => (
        <audio
          key={`popcat-audio-${index}`}
          className='popcat-audio-loader'
          ref={audioRef}
          src={popSounds[index]}
          autoPlay={false}
        />
      ))}
      <div className={`popcat${open ? ` is-pop` : ''}`} ref={popcat} />
      <img className='popcat-image-loader' src={catImg} />
      <img className='popcat-image-loader' src={popcatImg} />
      {popTexts.map(({ id, text, type, anchor }) => (
        <span
          key={id}
          className={`particle-add particle-add-${type ?? 0}`}
          style={
            {
              left: anchor?.x ?? 0,
              top: anchor?.y ?? 0
            } as React.CSSProperties
          }
          onAnimationEnd={() => {
            particleRemove(id);
          }}
        >
          {text}
        </span>
      ))}
    </div>
  );
};

export default Popcat;
