import { AbstractConnector } from '@web3-react/abstract-connector'
import { InjectedConnector } from '@web3-react/injected-connector';
import { WalletConnectConnector } from '@web3-react/walletconnect-connector'
import { UnsupportedChainIdError, useWeb3React } from '@web3-react/core'
import web3 from 'web3';
import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import MetamaskIcon from '../../assets/images/metamask.png'
import { SUPPORTED_WALLETS } from './supported-wallet'

import Toast from '@/components/toast';
import Model from '@/components/modal';

import { chains } from '@/utils/chainData';
import usePrevious from '@/hooks/usePrevious'

import './index.scss';

declare global {
  interface Window {
    ethereum: any
    web3: web3
  }
}

const injected = new InjectedConnector({
  supportedChainIds: chains.map(({ chainId }) => chainId)
});

const WALLET_VIEWS = {
  OPTIONS: 'options',
  OPTIONS_SECONDARY: 'options_secondary',
  ACCOUNT: 'account',
  PENDING: 'pending',
}

const WalletMedal = (props: any) => {
  const { t } = useTranslation();
  const context = useWeb3React();
  console.log(`debug -> WalletMedal -> context`, context);

  const { library, connector, account, activate, deactivate, active, error } = context;
  console.log(
    `debug -> WalletMedal -> { library, account, activate, deactivate, active, error }`,
    { library, account, activate, deactivate, active, error }
  );

  console.log(`debug -> updateBalance -> library?.network`, library?.network);
  console.log(
    `debug -> updateBalance -> window.library = library`,
    ((window as any).library = library)
  );
  console.log(
    `debug -> updateBalance -> window.web3 = web3`,
    ((window as any).web3 = web3)
  );

  const [walletView, setWalletView] = useState(WALLET_VIEWS.ACCOUNT)

  const [pendingWallet, setPendingWallet] = useState<AbstractConnector | undefined>()

  const [walletModalOpen, setWalletModalOpen] = useState<boolean>(false)
  const [disconnectModalOpen, setDisconnectModalOpen] = useState<boolean>(false)

  // always reset to account view
  useEffect(() => {
    if (walletModalOpen) {
      setWalletView(WALLET_VIEWS.ACCOUNT)
    }
  }, [walletModalOpen])

  // close modal when a connection is successful
  const activePrevious = usePrevious(active)
  const connectorPrevious = usePrevious(connector)
  useEffect(() => {
    if (walletModalOpen && ((active && !activePrevious) || (connector && connector !== connectorPrevious && !error))) {
      setWalletView(WALLET_VIEWS.ACCOUNT)
      setWalletModalOpen(false)
    }
  }, [setWalletView, active, error, connector, walletModalOpen, activePrevious, connectorPrevious])

  const tryActivation = async (connector: AbstractConnector | undefined) => {
    let name = ''
    Object.keys(SUPPORTED_WALLETS).map((key) => {
      if (connector === SUPPORTED_WALLETS[key].connector) {
        return (name = SUPPORTED_WALLETS[key].name)
      }
      return true
    })
    // log selected wallet
    // ReactGA.event({
    //   category: 'Wallet',
    //   action: 'Change Wallet',
    //   label: name,
    // })
    setPendingWallet(connector) // set wallet for pending view
    setWalletView(WALLET_VIEWS.PENDING)

    // if the connector is walletconnect and the user has already tried to connect, manually reset the connector
    if (connector instanceof WalletConnectConnector && connector.walletConnectProvider?.wc?.uri) {
      connector.walletConnectProvider = undefined
    }

    connector &&
      activate(connector, undefined, true).catch((error) => {
        if (error instanceof UnsupportedChainIdError) {
          activate(connector) // a little janky...can't use setError because the connector isn't set
        } else {
          Toast.fail({
            title: t('textWalletConnectFailed'),
            content: error?.message,
          });
        }
      })
  }

  function getOptions() {
    const isMetamask = window.ethereum && window.ethereum.isMetaMask
    return Object.keys(SUPPORTED_WALLETS).map((key) => {
      const option = SUPPORTED_WALLETS[key]

      // overwrite injected when needed
      if (option.connector === injected) {
        // don't show injected if there's no injected provider
        if (!(window.web3 || window.ethereum)) {
          if (option.name === 'MetaMask') {
            return (
              <div
                id={`connect-${key}`}
              >
                <a className='nes-btn' href="https://metamask.io/" target="_blank">
                  {t('textWalletInstallMetamask')}
                  <img src={MetamaskIcon} alt={'Icon'} />
                </a>
              </div>
            )
          } else {
            return null //dont want to return install twice
          }
        }
        // don't return metamask if injected provider isn't metamask
        else if (option.name === 'MetaMask' && !isMetamask) {
          return null
        }
        // likewise for generic
        else if (option.name === 'Injected' && isMetamask) {
          return null
        }
      }

      // return rest of options
      return (
        <div key={key}>
          <button
            id={`connect-${key}`}
            className={`nes-btn wallet-option ${option.disabled ? 'is-disabled' : ''}`}
            onClick={() => {
              option.connector === connector
                ? setWalletView(WALLET_VIEWS.ACCOUNT)
                : !option.href && tryActivation(option.connector)
            }}
          >
            <span>{option.name}</span>
            <img src={option.iconURL} alt={'Icon'} />
          </button>
        </div>
      )
    })
  }

  return (
    <>
      <Model
        isOpen={walletModalOpen}
        onDismiss={() => { setWalletModalOpen(false) }}
        title={t('textWalletConnect')}
      >
        <p style={{marginBottom: '20px'}}>By connecting a wallet, you agree to our’ Terms of Service and acknowledge.</p>
        {getOptions()}
      </Model>
      <Model
        isOpen={disconnectModalOpen}
        onDismiss={() => { setDisconnectModalOpen(false) }}
        title={t('btnDisconnect')}
      >
        <div style={{textAlign: 'center'}}>
          <p>Bye bye!</p>
          <button
            className='nes-btn is-warning'
            onClick={() => {
              deactivate();
              setDisconnectModalOpen(false)
            }}
          >
            {t('btnConfirm')}
          </button>
        </div>
      </Model>
      {active ? (
        <>
          <button
            className='nes-btn'
            onClick={() => {
              setDisconnectModalOpen(true)
            }}
          >
            {`${account?.slice(0, 6)}...${account?.slice(account.length - 3, account.length)}`}
          </button>
          {props.children}
        </>
      ) : (
        <>
          <button
            className='nes-btn is-primary'
            onClick={() => { setWalletModalOpen(true) }}
          >
            {t('textWalletConnect')}
          </button>
          {props.children}
        </>
      )}
    </>
  );
};

export default WalletMedal;
